html {
  font-size: 62.5%;
  box-sizing: border-box;
  scroll-padding-top: 0rem;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}
body.body-scroll {
  padding-top: 8.6rem;
}

p {
  font-size: 2.2rem;
  color: #000000;
  line-height: 1.5;
}

.contenedor {
  width: 85%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.header {
  background-color: rgba(0, 0, 0, 0.9254901961);
}
@media (min-width: 768px) {
  .header.fijo {
    left: 0;
    top: 0;
    position: fixed;
    width: 100%;
    box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
  }
}
.header h1 {
  color: aliceblue;
  text-align: center;
}
@media (min-width: 768px) {
  .header h1 {
    margin: 0;
  }
}
.header .contenido-header {
  padding: 1.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .contenido-header picture img {
  height: 4rem;
}
.navegacion-principal {
  display: flex;
  gap: 2rem;
}
@media (max-width: 700px) {
  .navegacion-principal {
    display: flex;
    flex-direction: column;
    gap: 0rem;
  }
}
.navegacion-principal a {
  display: block;
  text-align: center;
  color: aliceblue;
  font-size: 2rem;
  margin-bottom: 1rem;
  font-family: "Roboto", sans-serif;
}
@media (max-width: 700px) {
  .navegacion-principal a {
    margin-bottom: 0rem;
  }
}
.navegacion-principal a:hover {
  color: #CCDC31;
}

.inicio {
  position: relative;
  max-height: 90rem;
  min-height: 70rem;
  overflow: hidden;
  background-image: url(../../../build/img/FondoUno.webp);
  background-size: cover;
}
.inicio .overlay {
  position: absolute;
  background: rgba(31, 52, 90, 0.8078431373);
  display: flex;
  flex-direction: row;
  justify-content: center;
}
@media (max-width: 1000px) {
  .inicio .overlay {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.contenido-inicio {
  z-index: 10;
}
.contenido-inicio .typing-animation {
  overflow: hidden;
  white-space: nowrap;
  width: max-content;
}
.contenido-inicio .typing-border {
  border-right: 2px solid;
}
.contenido-inicio h2 {
  color: aliceblue;
  font-size: 7rem;
  font-weight: 500;
  margin: 0;
  margin-left: 10rem;
  font-family: "Roboto", sans-serif;
}
@media (max-width: 1000px) {
  .contenido-inicio h2 {
    margin: auto;
    font-size: 5rem;
  }
}
@media (max-width: 550px) {
  .contenido-inicio h2 {
    margin-top: -4rem;
  }
}
.contenido-inicio p {
  box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
  background-color: #CCDC31;
  display: inline-block;
  padding: 1rem 2rem;
  color: #1F345A;
  font-weight: 700;
  font-size: 2.4rem;
  margin: 0;
  margin-left: 10rem;
}
@media (max-width: 1000px) {
  .contenido-inicio p {
    margin-left: 0;
    font-size: 2rem;
  }
}

.overlayTodo {
  position: relative;
  overflow-x: hidden;
}
.overlayTodo .moving-image {
  position: relative;
  width: 7rem;
  height: 7rem;
  margin-top: -4rem;
  left: 40%;
  transform: translate(-50%, -50%);
  transition: transform 0.5s ease, opacity 0.5s ease;
}
@media (max-width: 550px) {
  .overlayTodo .moving-image {
    margin-left: 3.5rem;
  }
}
.overlayTodo .clicked {
  transform: translateX(200%);
  opacity: 0;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  display: grid;
  place-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}
.overlay picture {
  display: block;
  max-width: 80rem;
}

.sobre-Hackaton {
  padding: 5rem 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (max-width: 1000px) {
  .sobre-Hackaton {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 5rem;
  }
}
@media (max-width: 550px) {
  .sobre-Hackaton .imagen {
    margin-top: 4rem;
  }
}

.descriptionInicial {
  padding: 5rem 0;
  text-align: center;
}
.descriptionInicial h2 {
  color: #1F345A;
  font-size: 6rem;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
  margin-bottom: 0;
}
@media (max-width: 550px) {
  .descriptionInicial h2 {
    font-size: 5rem;
  }
}
.descriptionInicial .fecha {
  font-weight: 700;
  margin-top: 0;
}

.informacionDescubre {
  background-color: rgba(128, 128, 128, 0.574);
}

.informacionDesafia .contenido-desafia span {
  font-weight: 700;
  font-size: 3.5rem;
  color: #1F345A;
}
.informacionDesafia .contenido-desafia h2 {
  margin-top: 5rem;
  text-align: center;
  color: #1F345A;
}
.informacionDesafia .contenido-desafia ul {
  display: flex;
  flex-direction: row;
  list-style: none;
  margin-bottom: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
}
@media (max-width: 550px) {
  .informacionDesafia .contenido-desafia ul {
    margin-left: -6rem;
  }
}
.informacionDesafia .contenido-desafia ul h1 {
  margin-bottom: 2rem;
  text-align: center;
  color: #1F345A;
}
.informacionDesafia .contenido-desafia ul li {
  text-align: center;
  padding: 2rem;
  background-color: rgba(194, 220, 0, 0.9058823529);
  font-weight: 550;
  color: #1F345A;
  box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
}

.contenido-hackaton {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
@media (min-width: 768px) {
  .contenido-hackaton {
    margin-top: 0;
  }
}
.contenido-hackaton h2 {
  margin-bottom: 2rem;
}

.premios {
  position: relative;
  padding: 5rem 0;
}

.premios::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(31, 52, 90, 0.6392156863);
  z-index: -1;
}

.premios h3,
.premios p {
  margin-left: 10rem;
  margin-right: 10rem;
  color: aliceblue;
  text-align: center;
}
@media (max-width: 550px) {
  .premios h3,
  .premios p {
    margin: 0;
  }
}

.premios .descripcion {
  margin-left: 10rem;
  margin-right: 10rem;
  text-align: center;
}
@media (max-width: 550px) {
  .premios .descripcion {
    margin: 2rem;
    text-align: left;
  }
}

.premios .description {
  text-transform: uppercase;
}

.premios::before {
  content: "";
  background-image: url(../../../build/img/fondoOpcional.webp);
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0.95; /* Controla la opacidad de la imagen de fondo */
  z-index: -2; /* Asegura que la imagen de fondo esté detrás del contenido y del pseudo-elemento */
}

@media (min-width: 768px) {
  .premios-contenedor {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
  }
}
.premios-contenedor p {
  color: #000000;
}

.puesto {
  box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
  padding-top: 4rem;
  margin-bottom: 2rem;
}
.puesto .nombre-puesto {
  color: aliceblue;
}
.puesto .nombre-puesto:after {
  content: "";
  display: block;
  width: 10rem;
  height: 10rem;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 2rem auto 0 auto;
  text-align: center;
}

.primero .nombre-puesto:after {
  background-image: url(../../../build/img/Primero.webp);
  background-size: contain;
}

.segundo .nombre-puesto:after {
  background-image: url(../../../build/img/segundo.webp);
  background-size: contain;
}

.tercero .nombre-puesto:after {
  background-image: url(../../../build/img/tercero.webp);
  background-size: contain;
}

.cuarto .nombre-puesto:after {
  background-image: url(../../../build/img/cuarto.webp);
  background-size: contain;
}

.listado {
  margin: 0;
  padding: 0;
  list-style: none;
}
.listado li {
  color: aliceblue;
  padding: 2.5rem;
  font-weight: 700;
}
.listado li span {
  font-weight: 400;
}

.bg-amarillo {
  background-color: rgba(0, 0, 0, 0.4666666667);
}
.bg-amarillo li:nth-child(odd) {
  background-color: rgba(0, 0, 0, 0.4941176471);
}
.bg-amarillo li:nth-child(odd):hover {
  background-color: rgba(171, 186, 32, 0.4117647059);
}

.bg-verde {
  background-color: rgba(0, 0, 0, 0.4941176471);
}
.bg-verde li:nth-child(odd) {
  background-color: rgba(113, 108, 145, 0.168627451);
}
.bg-verde li:nth-child(odd):hover {
  background-color: rgba(171, 186, 32, 0.4117647059);
}

.participa {
  background-color: aliceblue;
  justify-content: center;
  align-items: center;
}
@media (max-width: 550px) {
  .participa {
    width: 100%;
  }
}
.participa .contenedor-participa {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.participa h3 {
  text-align: center;
  margin-top: 4rem;
  margin-bottom: 3rem;
}
.participa h4 {
  text-align: center;
  margin: 5rem;
  font-size: 2.5rem;
}
.participa p {
  margin: 5rem;
}
@media (max-width: 550px) {
  .participa p {
    margin: 2rem;
  }
}
.participa ul {
  display: flex;
  flex-direction: column;
  list-style: none;
  margin-bottom: 5rem;
}
.participa ul li {
  padding: 3rem;
  font-weight: 700;
  font-size: 2rem;
}

.patrocinan .contenedor .imagen {
  box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
}

.call-action {
  box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
  background-color: #CCDC31;
  color: #1F345A;
  font-weight: 700;
  font-size: 2rem;
  text-align: center;
  width: 80%;
  max-width: 25rem;
  padding: 2rem;
  margin-bottom: 3rem;
}
@media (max-width: 700px) {
  .call-action {
    margin-bottom: 2rem;
  }
}
.call-action:hover {
  transition: background-color 1s ease;
  background-color: #1F345A;
  color: aliceblue;
}

.site-footer {
  background-color: #3A3A3A;
  padding: 2rem;
}
.site-footer .contenido-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.site-footer .footer-contenido .navegacion-principal a {
  padding: 1rem;
}

.formulario input {
  color: rgb(0, 0, 0);
}
.formulario p {
  font-size: 1.4rem;
  color: aliceblue;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: aliceblue;
}
.formulario label {
  text-transform: uppercase;
  display: block;
  color: aliceblue;
}
.formulario fieldset {
  background-color: rgba(17, 21, 39, 0.342);
  border: 1px solid #3A3A3A;
  border-radius: 1rem;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  border: 1px solid #3A3A3A;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 20rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}

.contenedor-formulario {
  width: 50rem;
  margin-right: 13rem;
}
@media (max-width: 1000px) {
  .contenedor-formulario {
    margin-right: 0;
    margin-top: 4rem;
  }
}
@media (max-width: 550px) {
  .contenedor-formulario {
    width: 35rem;
    margin-top: 2rem;
  }
}
.contenedor-formulario h1 {
  text-align: center;
  color: aliceblue;
  font-weight: 700;
  font-family: "Roboto", sans-serif;
}
@media (max-width: 550px) {
  .contenedor-formulario h1 {
    font-size: 3rem;
  }
}
.contenedor-formulario .boton-enviar {
  color: #1F345A;
  box-shadow: 0.1rem 0.2rem 0.3rem #3A3A3A;
  transition: background-color 1s ease;
  font-weight: 700;
  background-color: #CCDC31;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: inline-block;
  border: none;
}
.contenedor-formulario .boton-enviar:hover {
  box-shadow: 0.1rem 0.2rem 0.3rem aliceblue;
  transition: background-color 1s ease;
  background-color: #1F345A;
  color: aliceblue;
}